import React from 'react'
import {useState,useEffact} from 'react'

import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";

import './index.css'

const WebFoodCard = ({food,language}) =>{
   
    
    return (
        <div className="WebFoodCard-container">
            <img className="WebFoodCard-image" src={food.img}></img>
            <div className="WebFoodCard-name">{language=='CN'?food.CN:food.EN}</div>
            <div className="WebFoodCard-price">{'$ '+food.price}</div>
        </div>
    )
}

const MobileFoodCard = ({food,language}) =>{

    return (
        <div className="MobileFoodCard-container">
            <img className="MobileFoodCard-image" src={food.img}></img>
            <div className="MobileFoodCard-name">{language=='CN'?food.CN:food.EN}</div>
            <div className="MobileFoodCard-price">{'$ '+food.price}</div>
        </div>
    )
}

export {WebFoodCard,MobileFoodCard}
