import React from 'react'
import {useState,useEffact} from 'react'

import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";
import {WebHeader, MobileHeader} from './header'
import {WebIntroduction, MobileIntroduction} from './Introduction'
import {WebOverlook,MobileOverlook} from './Overlook'
import {WebMenu,MobileMenu} from './Menu'

const Index = () =>{
    const [language,setlanguage]=useState('EN')

    const WebView = (<div className="WebView-container">
        <WebHeader languageMap={[language,setlanguage]}/>
        <div className="WebView-body">
            <WebIntroduction languageMap={[language,setlanguage]}/>
            <WebOverlook />
            <WebMenu  languageMap={[language,setlanguage]}/>
        </div>
    </div>)

    const MobileView = (<div className="MobileView-container">
        <MobileHeader languageMap={[language,setlanguage]}/>
        <div className="MobileView-body">
            <MobileIntroduction languageMap={[language,setlanguage]}/>
            <WebOverlook />
            <MobileMenu  languageMap={[language,setlanguage]}/>
        </div>

    </div>)

    return isBrowser?WebView:MobileView
}

export default Index