import React from 'react'
import {useState,useEffact} from 'react'

import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";

import './css/index.css'
import './css/Overlook.css'
import background from '../assert/images/background1.jpg'

const WebOverlook = ({languageMap}) =>{

    return (
        <div className="WebOverlook-container">
            <img className="WebOverlook-background" src={background}></img>
        </div>
    )
}

const MobileOverlook = ({languageMap}) =>{

    return (
        <div className="MobileOverlook-container">
            <img className="MobileOverlook-background" src={background}></img>
        </div>
    )
}

export {WebOverlook,MobileOverlook}
