import React from 'react'
import {useState,useEffact} from 'react'

import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";

import './css/index.css'
import './css/Introduction.css'
import logo2 from '../assert/images/logo2.jpg'
import WebLocation from '../assert/images/WebLocation.png'
import MobileLocation from '../assert/images/MobileLocation.png'

const WebIntroduction = ({languageMap}) =>{
    const [language,setlanguage]=languageMap
    let labelsLanguage = (language=='CN'?labels_cn:labels_en)

    return (
        <div className="WebIntroduction-container">
            <img className="WebIntroduction-logo" src={logo2}></img>
            <div className="WebIntroduction-labels">
                <div className="WebIntroduction-label-title">{labelsLanguage.title}</div>
                <div className="WebIntroduction-label-normal">{labelsLanguage.address1}</div>
                <div className="WebIntroduction-label-normal">{labelsLanguage.address2}</div>
                <div className="WebIntroduction-label-tel">✆&nbsp;(07) 3832 9688</div>
                <div className="WebIntroduction-label-normal">{labelsLanguage.openingDay}</div>
                <div className="WebIntroduction-label-openingTime">{labelsLanguage.openingTime1}</div>
                <div className="WebIntroduction-label-openingTime">{labelsLanguage.openingTime2}</div>
            </div>
            <img className="WebIntroduction-location" src={WebLocation}></img>
        </div>
    )
}

const MobileIntroduction = ({languageMap}) =>{
    const [language,setlanguage]=languageMap
    const labelsLanguage = (language=='CN'?labels_cn:labels_en)

    return (
        <div className="MobileIntroduction-container">
            <div className="MobileIntroduction-line">
                <img className="MobileIntroduction-logo" src={logo2}></img>
                <div className="MobileIntroduction-labels">
                    <div className="MobileIntroduction-label-title">{labelsLanguage.title}</div>
                    <div className="MobileIntroduction-label-normal">{labelsLanguage.address1}</div>
                    <div className="MobileIntroduction-label-normal">{labelsLanguage.address2}</div>
                    <div className="MobileIntroduction-label-tel">✆&nbsp;(07) 3832 9688</div>
                    <div className="MobileIntroduction-label-normal">{labelsLanguage.openingDay}</div>
                    <div className="MobileIntroduction-label-openingTime">{labelsLanguage.openingTime1}</div>
                    <div className="MobileIntroduction-label-openingTime">{labelsLanguage.openingTime2}</div>
                </div>
            </div>
            <div className="MobileIntroduction-line">
                <img className="MobileIntroduction-location" src={MobileLocation}></img>
            </div>
        </div>
    )
}
export {WebIntroduction,MobileIntroduction}

const labels_cn={
    title:'布里斯班',
    address1:'3/466 Boundary 街',
    address2:'Spring Hill QLD 4000',
    openingDay:'周一到周五',
    openingTime1:'早上10:30 - 下午14:30',
    openingTime2:'下午17:00 - 晚上20:30',
}

const labels_en={
    title:'Brisbane',
    address1:'3/466 Boundary Street',
    address2:'Spring Hill QLD 4000',
    openingDay:'Monday - Friday',
    openingTime1:'10:30am - 14:30pm',
    openingTime2:'17:00pm - 20:30pm',
}