import React from 'react'
import {useState,useEffact} from 'react'

import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";

import './css/index.css'
import './css/Menu.css'
import background from '../assert/images/background1.jpg'
import categories from '../constants/category'
import foods from '../constants/food'

import {WebFoodCard,MobileFoodCard} from '../components/FoodCard'


const WebMenu = ({languageMap}) =>{
    const [language,setlanguage]=languageMap
    let labelsLanguage = (language=='CN'?labels_cn:labels_en)
    const [currentCategory, setcurrentCategory] = useState('All')
    const [foodList, setfoodList] = useState(foods)
    const getFoodList = (name) =>{
        let newList = []
        foods.forEach(food => {
            if(name=='All' || food.category==name){
                newList.push(food)
            }
        });
        setfoodList(newList)
    }
    const handleCategoryOnClick = (name) =>{
        setcurrentCategory(name)
        getFoodList(name)
    }
    return (
        <div className="WebMenu-container">
            <div className="WebMenu-title">{labelsLanguage.Menu}</div>
            {/* <div className="WebMenu-divider" /> */}
            <div className="WebMenu-content">
                {/* left part */}
                <div className="WebMenu-filter">
                    <div className="WebMenu-filterTitle">{labelsLanguage.filterName}</div>
                    <div className="WebMenu-divider" />
                    <div className="WebMenu-categories">
                        {categories.map((category,index)=>(
                            <div className={currentCategory==category.EN?'WebMenu-category-selected':'WebMenu-category-unselected'}
                                onClick={()=>handleCategoryOnClick(category.EN)}
                            >
                                {language=='CN'?category.CN:category.EN}
                            </div>
                        ))}
                    </div>
                </div>
                {/* right part */}
                <div className="WebMenu-itemList">
                    {foodList.map((food,index)=>(<WebFoodCard food={food} language={language} />))}
                </div>
            </div>

        </div>
    )
}

const MobileMenu = ({languageMap}) =>{
    const [language,setlanguage]=languageMap
    let labelsLanguage = (language=='CN'?labels_cn:labels_en)
    const [currentCategory, setcurrentCategory] = useState('All')
    const [foodList, setfoodList] = useState(foods)
    const getFoodList = (name) =>{
        let newList = []
        foods.forEach(food => {
            if(name=='All' || food.category==name){
                newList.push(food)
            }
        });
        setfoodList(newList)
    }
    const handleCategoryOnClick = (name) =>{
        setcurrentCategory(name)
        getFoodList(name)
    }
    return (
        <div className="MobileMenu-container">
            <div className="MobileMenu-title">{labelsLanguage.Menu}</div>
            <div className="MobileMenu-filter">
                <div className="MobileMenu-filterTitle">{labelsLanguage.filterName}</div>
                <select className="MobileMenu-filterSelect" 
                    value={currentCategory.EN}
                    onChange={e=>handleCategoryOnClick(e.target.value)}
                >
                    {categories.map((category,index)=>(
                        <option value={category.EN}>
                            {language=='CN'?category.CN:category.EN}
                        </option>
                    ))}
                </select>
            </div>
            <div className="MobileMenu-itemList">
                {foodList.map((food,index)=>(<MobileFoodCard food={food} language={language} />))}
            </div>
        </div>
    )
}

export {WebMenu,MobileMenu}

const labels_cn={
    Menu:'菜单',
    filterName:'类别',
}

const labels_en={
    Menu:'Menu',
    filterName:'Filter by',
}