import React from 'react'
import {useState,useEffact} from 'react'

import {BrowserView,MobileView,isBrowser,isMobile} from "react-device-detect";

import './css/index.css'
import './css/Header.css'
import logo1 from '../assert/images/logo1.png'

const WebHeader = ({languageMap}) =>{
    const [language,setlanguage]=languageMap

    return (
        <div className="Webheader-container">
            <img className="Webheader-logo" src={logo1}></img>
            <div className="Webheader-labels">
                <div className="Webheader-label-button" onClick={()=>setlanguage('CN')}>中文</div>
                <div className="Webheader-label-button" onClick={()=>setlanguage('EN')}>English</div>
                <div className="Webheader-label-normal">✆&nbsp;(07) 3832 9688</div>
            </div>
        </div>
    )
}

const MobileHeader = ({languageMap}) =>{
    const [language,setlanguage]=languageMap

    return (
        <div className="Mobileheader-container">
            <img className="Mobileheader-logo" src={logo1}></img>
            <div className="Mobileheader-labels">
                <div className="Mobileheader-label-button" onClick={()=>setlanguage('CN')}>中文</div>
                <div className="Mobileheader-label-button" onClick={()=>setlanguage('EN')}>English</div>
                <div className="Mobileheader-label-normal">✆&nbsp;(07) 3832 9688</div>
            </div>
        </div>
    )
}

export {WebHeader,MobileHeader}
